

form{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    width: 50%;
    height: 50%;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 0 10px 0 rgba(0,0,0,0.2);
    padding: 20px;
    margin-top: 50px;
    margin-bottom: 50px;
    font-family: 'Roboto', sans-serif;
    font-size: 1.2rem;
    font-weight: 300;
    color: #000;
    text-align: start;
}

#contact {
    padding-top: 70px;
}

form label{
    margin-top: 20px;
    margin-bottom: 10px;
    font-size: 1.2rem;
    font-weight: 300;
    color: #000;
    text-align: start;
}

form input{
    width: 100%;
    height: 40px;
    border-radius: 5px;
    border: 1px solid #000;
    padding: 5px;
    margin-bottom: 20px;
    font-family: 'Roboto', sans-serif;
    font-size: 1.2rem;
    font-weight: 300;
    color: #000;
    text-align: center;
}

form textarea{
    width: 100%;
    height: 100px;
    border-radius: 5px;
    border: 1px solid #000;
    padding: 5px;
    margin-bottom: 20px;
    font-family: 'Roboto', sans-serif;
    font-size: 1.2rem;
    font-weight: 300;
    color: #000;
    text-align: center;
}

form input[type="submit"]{
    width: 100%;
    height: 40px;
    border-radius: 5px;
    border: 1px solid #000;
    padding: 5px;
    margin-bottom: 20px;
    font-family: 'Roboto', sans-serif;
    font-size: 1.2rem;
    font-weight: 300;
    color: #000;
    text-align: center;
 background: linear-gradient(to bottom right, #5ca5ea, #7793f8);
    color: #fff;
    cursor: pointer;
}

form input[type="submit"]:hover{
    background-color: #fff;
    color: #000;
    transition: 0.5s;
}

@media screen and (max-width: 768px){
    form{
        width: 80%;
    }
}

#success{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    width: 50%;
    height: 50%;
    background-color: #fff;
    border-radius: 10px;
    padding: 20px;
    margin-top: 50px;
    margin-bottom: 50px;
    font-family: 'Roboto', sans-serif;
    font-size: 1.2rem;
    font-weight: 300;
    color: #31db80;
    text-align: center;

    animation: fadeOut 2s ease-in-out forwards;
}


@media (max-width: 768px) {
    #contact {
        padding-top: 70px;
    }
}